<template>
  <!-- 名称和类型 -->
  <el-form :model="model" :rules="rules" size="medium" ref="form">
    <section class="form-section clearfix">
      <el-form-item prop="modelNumber" :label="$t('model.info.style_no')">
        <el-input
          v-model="model.modelNumber"
          :disabled="!hasPermission('OP0015', 'OP0141')"
          :placeholder="$t('model.info.input_placeholder')" />
      </el-form-item>

      <el-form-item prop="type" :label="$t('model.info.product_type')">
        <dictionary-selector
          :value="model.type"
          :disabled="!isCreator"
          type="handbagType"
          :placeholder="$t('model.info.shoe_category')"
          class="width-100"
          @on-change="changeProperty('type', $event)" />
      </el-form-item>
    </section>

    <!-- 鞋标签 -->
    <section class="form-section clearfix" style="margin: 0 0 5px;">
      <h2 class="section-title">
        {{ $t('model.info.tags') }}
      </h2>
      <TagsEditor :tags="model.tagsInfo"
        :shareId="model.id" :type="3" :relateType="3" :editable="hasPermission('OP0015' ,'OP0141')" />
    </section>

    <!-- 备注信息 -->
    <section class="form-section clearfix" style="margin-right: 0px;">
      <h2 class="section-title">
        {{ $t('model.info.notes') }}
      </h2>
      <el-input
        v-model="model.notes"
        :disabled="!hasPermission('OP0015', 'OP0141')"
        :placeholder="$t('model.info.input_placeholder')"
        :rows="3"
        type="textarea"
        resize="none"
        clearable
        maxlength="200"
        show-word-limit />
    </section>

    <!-- 鞋基本信息 -->
    <section class="form-section clearfix">
      <el-form-item
        v-for="item in basicList"
        :key="item.label"
        :label="item.label"
        :prop="item.prop">
        <el-input
          v-model="model[item.prop]"
          :disabled="!isCreator"
          :placeholder="$t('model.info.input_placeholder')" />
      </el-form-item>

      <el-form-item prop="price" :label="$t('model.info.prices')">
        <input-with-unit
          :value="model.price"
          :disabled="!isCreator"
          :default-unit="model.priceUnitObject.itemValue"
          :unit-options="[
            { value: 2, label: $i18n.i18next.language === 'en' ? 'USD' : '美元' },
            { value: 1, label: $i18n.i18next.language === 'en' ? 'CNY' : '人民币' }
          ]"
          :inputPlaceholder="$t('model.info.input_placeholder')"
          @on-input-blur="changeProperty('price', $event)"
          @on-select-change="changeProperty('monetaryUnit', $event)" />
      </el-form-item>
    </section>
  </el-form>
</template>

<script>
import Vue from 'vue';

import i18n from 'i18next';
import axios from '@/api/axios';
import AppError from '@/utils/error';
import formRules from '@/views/model/info/rules';
import TagsEditor from '@/components/base/TagsEditor.vue';
import mixinHasPermission from '../../mixinHasPermission';
import { getContextUser } from '../../../../../loaders/context';
import DictionarySelector from './DictionarySelector';
import InputWithUnit from './InputWithUnit';

async function updateBagInfo(data) {
  return axios({
    method: 'post',
    url: '/bff-center/showCommon/handbagInfoOperation/updateHandbagInfo',
    data,
  });
}

export default Vue.extend({
  name: 'ModelDetailForm',
  components: {
    TagsEditor,
    DictionarySelector,
    InputWithUnit,
  },
  props: {
    model: {
      type: Object,
      required: true,
    },
  },
  mixins: [mixinHasPermission],
  data() {
    return {
      rules: formRules,
      basicList: [
        { label: i18n.t('model.info.brand'), prop: 'brand' },
        { label: i18n.t('model.info.styleName'), prop: 'styleName' },
        { label: i18n.t('model.info.season'), prop: 'season' },
        { label: i18n.t('model.info.collection'), prop: 'series' },
        // { label: i18n.t('model.info.last_number'), prop: 'lastNumber' },
        { label: i18n.t('model.info.customer'), prop: 'customer' },
        // { label: i18n.t('model.info.outsole_number'), prop: 'soleNumber' },
        { label: i18n.t('model.info.designer'), prop: 'stylist' },
        // { label: i18n.t('model.info.heel_number'), prop: 'heelNumber' },
      ],
    };
  },
  computed: {
    isCreator() {
      return getContextUser().id === this.model.creator;
    },
  },
  methods: {
    // 修改鞋模型信息
    async changeModelInfo() {
      // const groupAppId = this.$closest('ModelInfo').groupAppId || '';
      // 先校验填写内容是否通过表单校验
      const valid = await this.$refs.form.validate();
      if (!valid) {
        throw AppError.abort;
      }

      // 发出更新数据请求
      try {
        await updateBagInfo({
          id: this.model.id,
          modelNumber: this.model.modelNumber,
          type: this.model.type,
          notes: this.model.notes,
          season: this.model.season,
          series: this.model.series,
          customer: this.model.customer,
          stylist: this.model.stylist,
          price: this.model.price,
          size: this.model.size,
          factory: this.model.factory,
          purpose: this.model.purpose,
          priceUnit: this.model.priceUnitObject.itemValue,
          brand: this.model.brand,
          styleName: this.model.styleName,
        });
      } catch (err) {
        this.$errHandler(err);
      }
    },
    // 处理其他组件更新的事件
    changeProperty(prop, value) {
      if (prop === 'monetaryUnit') {
        this.model.priceUnitObject.itemValue = value;
        return;
      }
      // 先要把数据同步到当前的model，否则表单对当前属性检验是旧数据
      this.model[prop] = value;
      // this.changeModelInfo(prop, value);
    },
  },
});
</script>

<style lang="scss" scoped>
.el-form {
  .form-section {
    margin-right: -20px;
    margin-bottom: 15px;

    .el-form-item {
      float: left;
      width: 300px;
      margin-right: 15px;
      margin-bottom: 0;
    }
  }

  /deep/ .el-form-item__label {
    float: none;
  }
}

// 公共样式
.section-title {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 600;
  color: #333;
  .section-subtitle {
    font-weight: 700;
  }
}
</style>
