















import axios from '@/api/axios';
import { i18n } from '@/i18n';
import {
  defineComponent, ref, onMounted, watch,
} from '@vue/composition-api';

interface OptionItem {
  label: string
  flag: number
  sort: number
  itemCode: string
  itemValue: number
  typeCode: string
  language: string
}

const getOptions = async () => {
  return axios({
    url: '/bff-center/showCommon/getDictionaryTypeItem',
    method: 'get',
    params: {
      typeCode: 'handbagType',
    },
    headers: {
      language: i18n.language,
    },
  });
};

const DictionarySelector = defineComponent({
  name: 'DictionarySelector',
  props: {
    value: {
      type: Number,
    },
    disabled: {
      type: Boolean,
    },
    placeholder: {
      type: String,
    },
  },
  setup(props, { emit }) {
    const selectedValue = ref(props.value);
    const options = ref<OptionItem[]>([]);

    function handleChange() {
      emit('on-change', selectedValue.value);
    }

    onMounted(async () => {
      options.value = (await getOptions()).data;
    });

    watch(() => props.value, () => {
      selectedValue.value = props.value;
    });

    return {
      selectedValue,
      options,
      handleChange,
    };
  },
});

export default DictionarySelector;
