<template>
  <div class="detail-text">
    <form-field-text :label="$t('model.info.style_no')" :content="model.modelNumber" />
    <form-field-text :label="$t('model.info.product_type')"
      :content="model.typeObject && model.typeObject.label" />
    <form-field-text :label="$t('model.info.notes')" :content="model.notes" :ellipsis="false" />

    <!-- 标签 -->
    <form-field-text :label="$t('model.info.tags')" :content="model.tagsInfo && model.tagsInfo.length">
      <ul class="tags-list">
        <li
          v-for="(item, index) in model.tagsInfo"
          :key="index"
          class="el-tag">{{ item.tag }}</li>
      </ul>
    </form-field-text>

    <ul class="section-half">
      <li v-for="field in generateFields()" :key="field.label" class="form-field">
        <form-field-text
          :label="field.label"
          :content="field.content || ''"
          :unit="field.unit" />
      </li>
    </ul>
  </div>
</template>

<script>
import i18n from 'i18next';

import FormFieldText from '@/components/form/FormFieldText.vue';

export default {
  name: 'ModelDetailText',
  components: {
    FormFieldText,
  },
  props: {
    model: {
      type: Object,
      required: true,
    },
  },
  methods: {
    generateFields() {
      const {
        season, series, customer, stylist, price, monetaryUnit, brand, styleName,
      } = this.model;

      return [
        { label: '品牌', content: brand },
        { label: '款名', content: styleName },
        { label: i18n.t('model.info.season'), content: season },
        { label: i18n.t('model.info.collection'), content: series },
        // { label: i18n.t('model.info.last_number'), content: lastNumber },
        { label: i18n.t('model.info.customer'), content: customer },
        // { label: i18n.t('model.info.outsole_number'), content: soleNumber },
        { label: i18n.t('model.info.designer'), content: stylist },
        // { label: i18n.t('model.info.heel_number'), content: heelNumber },
        { label: i18n.t('model.info.price'), content: price, unit: monetaryUnit },
      ];
    },
  },
};
</script>

